import $ from "jquery";

import BpmnModeler from "bpmn-js/lib/Modeler";
import resizeTask from "bpmn-js-task-resize/lib";

var container = $(".bpmn-render-wrap");

var modeler = new BpmnModeler({
  additionalModules: [resizeTask],
  taskResizingEnabled: true,
  container: "#canvas",
  keyboard: {
    bindTo: window,
  },
});

var elementId = "#canvas";
var clipboard = modeler.get("clipboard"),
  copyPaste = modeler.get("copyPaste"),
  elementRegistry = modeler.get("elementRegistry");

// get element to be copied
var element = elementRegistry.get(elementId);

// copy!
copyPaste.copy(element);

// retrieve clipboard contents
var copied = clipboard.get();
console.log(copied);

modeler.on("element.click", function (event) {
  var selectedElement = event.element;
  var selectedElements = modeler.get("selection").get();

  if (selectedElement.type === "bpmn:Participant" && selectedElements.length) {
    $.ajax({
      type: "post",
      url: "mdprocessflow/getRole",
      data: {
        mainBpId: 1453876478231,
      },
      beforeSend: function () {},
      success: function (data) {
        var comboDatas = [];
        var roleHtml = $(".bpmn-role-config");
        var $select = roleHtml.find("select");
        $select.empty();
        $select.append($("<option />").val("").text("- Cонгох -"));
        if (data && data.length > 0) {
          $.each(data, function (key, row) {
            if (
              selectedElement.businessObject.$attrs["roleid"] &&
              selectedElement.businessObject.$attrs["roleid"].indexOf(
                row.ROLE_ID
              ) !== -1
            ) {
              $select.append(
                $("<option />")
                  .val(row.ROLE_ID)
                  .text(row.ROLE_NAME)
                  .attr("selected", "selected")
              );
            } else {
              $select.append(
                $("<option />").val(row.ROLE_ID).text(row.ROLE_NAME)
              );
            }
            comboDatas.push({
              id: row.ROLE_ID,
              text: row.ROLE_NAME,
            });
          });
        }
        comboDatasBpmnFunction(comboDatas);
        $(".bpmn-role-config").removeClass("hidden");
      },
      error: function () {
        alert("Error");
      },
    });
  } else {
    $(".bpmn-role-config").addClass("hidden");
  }
});

