function createNewDiagram() {
  openDiagram(diagramXML);
}

async function openDiagram(xml) {
  try {
    await modeler.importXML(xml);

    container.removeClass("with-error").addClass("with-diagram");
  } catch (err) {
    container.removeClass("with-diagram").addClass("with-error");

    container.find(".error pre").text(err.message);

    console.error(err);
  }
}

function registerFileDrop(container, callback) {
  function handleFileSelect(e) {
    e.stopPropagation();
    e.preventDefault();

    var files = e.dataTransfer.files;

    var file = files[0];

    var reader = new FileReader();

    reader.onload = function (e) {
      var xml = e.target.result;

      callback(xml);
    };

    reader.readAsText(file);
  }

  function handleDragOver(e) {
    e.stopPropagation();
    e.preventDefault();

    e.dataTransfer.dropEffect = "copy"; // Explicitly show this is a copy.
  }

  container.get(0).addEventListener("dragover", handleDragOver, false);
  container.get(0).addEventListener("drop", handleFileSelect, false);
}

// file drag / drop ///////////////////////

// check file api availability
if (!window.FileList || !window.FileReader) {
  window.alert(
    "Looks like you use an older browser that does not support drag and drop. " +
      "Try using Chrome, Firefox or the Internet Explorer > 10."
  );
} else {
  registerFileDrop(container, openDiagram);
}

// bootstrap diagram functions

$(function () {
  createNewDiagram();

  var downloadLink = $("#js-download-diagram");
  var downloadSvgLink = $("#js-download-svg");

  $(".buttons a").click(function (e) {
    if (!$(this).is(".active")) {
      e.preventDefault();
      e.stopPropagation();
    }
  });

  function setEncoded(link, name, data) {
    var encodedData = encodeURIComponent(data);

    if (data) {
      link.addClass("active").attr({
        href: "data:application/bpmn20-xml;charset=UTF-8," + encodedData,
        download: name,
      });
    } else {
      link.removeClass("active");
    }
  }

  var exportArtifacts = debounce(async function () {
    try {
      const { svg } = await modeler.saveSVG();

      setEncoded(downloadSvgLink, "diagram.svg", svg);
    } catch (err) {
      console.error("Error happened saving svg: ", err);
      setEncoded(downloadSvgLink, "diagram.svg", null);
    }

    try {
      const { xml } = await modeler.saveXML({ format: true });
      setEncoded(downloadLink, "diagram.bpmn", xml);
    } catch (err) {
      console.error("Error happened saving XML: ", err);
      setEncoded(downloadLink, "diagram.bpmn", null);
    }
  }, 500);

  modeler.on("commandStack.changed", exportArtifacts);

  async function exportDiagram2() {
    try {
      var bpmRegistry = modeler.get("elementRegistry");
      var elements = bpmRegistry._elements,
        domainbp = [];
      var result = await modeler.saveXML({ format: true });

      Core.blockUI({
        message: "Loading...",
        boxed: true,
      });

      $.ajax({
        type: "post",
        url: "mdprocessflow/saveBpmnDraftIndicator",
        data: {
          xml: result.xml,
          mainBpId: indicatorId,
        },
        dataType: "json",
        async: false,
      });

      $.ajax({
        type: "post",
        url: "mdprocessflow/saveBpmnIndicator",
        data: {
          xml: result.xml,
          mainBpId: indicatorId,
        },
        dataType: "json",
        beforeSend: function () {
          Core.blockUI({
            message: "Loading...",
            boxed: true,
          });
        },
        success: function (data) {
          if (data.status === "success") {
            if (
              $("#dialog-bpmnEditor").length &&
              $("#dialog-bpmnEditor").is(":visible")
            ) {
              $("#dialog-bpmnEditor").dialog("close");
            }
            new PNotify({
              title: "Success",
              text: "Амжилттай хадгаллаа",
              type: "success",
              sticker: false,
            });
          } else {
            new PNotify({
              title: "Error",
              text: data.text,
              type: "warning",
              sticker: false,
            });
          }
          Core.unblockUI();
        },
        error: function () {
          alert("Error");
        },
      });
      console.log(result.xml);
    } catch (err) {
      console.error("could not save BPMN 2.0 diagram", err);
    }
  }

  $("#bpmn-save-button").click(exportDiagram2);
});

window.domainbpValueSetToModeling = (row) => {
  var selectedElements = modeler.get("selection").get();
  var modeling = modeler.get("modeling");

  if (!selectedElements[0] || selectedElements[0]["type"] !== "bpmn:Task") {
    alert("Process таск дээр тохируулах боломжтой");
    return;
  }

  selectedElements[0].businessObject.set("processid", row.id);
  modeling.updateProperties(selectedElements[0], { name: row.name });
  modeling.setColor(selectedElements[0], { stroke: "#000" });
};

window.comboValueSetToModeling = (values, text) => {
  var selectedElements = modeler.get("selection").get();
  var modeling = modeler.get("modeling");

  selectedElements[0].businessObject.set("roleid", values.join(","));
  modeling.updateProperties(selectedElements[0], { name: text });
};

window.bpmnExportXml = async () => {
  var result = await modeler.saveXML({ format: true });
  var dataStr = "data:text/plain;charset=utf-8," + result.xml;
  var dlAnchorElem = document.getElementById("downloadAnchorElem");
  dlAnchorElem.setAttribute("href", dataStr);
  dlAnchorElem.setAttribute(
    "download",
    "bpmn_export" + window.getUniqueId(1) + ".xml"
  );
  dlAnchorElem.click();
};

window.bpmnImportXml = () => {
  document.getElementById("bpmn_hidden_file_input").click();
};

window.bpmnOpenFile = (event) => {
  var input = event.target;

  var reader = new FileReader();
  reader.onload = function () {
    var text = reader.result;
    modeler.importXML(text);
  };
  reader.readAsText(input.files[0]);
};

// helpers //////////////////////

function debounce(fn, timeout) {
  var timer;

  return function () {
    if (timer) {
      clearTimeout(timer);
    }

    timer = setTimeout(fn, timeout);
  };
}
